<template>
  <wrapper>
    <v-card flat>
      <v-card-text>
        <v-row>
          <v-col cols="12" class="text-center">
            <v-avatar size="200px">
              <v-img v-if="$usuario && $usuario.refImagen" height="200" width="200" :src="$usuario.refImagen"/>
              <v-img v-else height="200" width="200" src="../../assets/tmp_52191a9c-81e4-40ad-8767-13d4ecea97ca.jpg" />
            </v-avatar>
          </v-col>

          <v-col cols="12">
            <v-tabs centered>
              <v-tab>
                Perfil
              </v-tab>
              <v-tab>
                Datos fiscales
              </v-tab>

              <v-tab-item>
                <v-card>
                  <v-card-text>
                    <v-row>
                      <v-col cols="12" class="text-h4 text-md-h3 pb-0">
                        {{ $usuario.nombreCompleto }}<v-btn icon x-small @click="abrirDialogEditar"><v-icon>mdi-lead-pencil</v-icon></v-btn>
                      </v-col>
                      <v-col cols="12" class="text-h5 text-md-h5 pb-0">
                        {{ $usuario.email }}
                      </v-col>
                      <v-col cols="12" class="text-h5 text-md-h5">
                        {{ $usuario.telefono }}
                      </v-col>
                      <div v-if="this.$usuario.esAlumno">
                        <v-col cols="12" class="text-h5 text-md-h4 pb-0">
                          Matrícula
                        </v-col>
                        <v-col cols="12" class="text-h5 text-md-h4 pb-0">
                          2° Preparatoria
                        </v-col>
                      </div>
                    </v-row>
                  </v-card-text>
                </v-card>

              </v-tab-item>

              <v-tab-item class="text-center py-2 mt-4" style="display: flex;justify-content: center;">
              <v-card v-if="this.datosFiscales.datosDeFacturacion != null" class="text-left" style="border-radius: 20px; font-family: Nunito; border-color: #1867c0;border-style: solid"
                      width="500">
                <v-card-title class="pa-2 text-center"
                              style="background-color: gray;display: flex;margin-left: 90%;
                    border-bottom-left-radius: inherit">
                  <v-btn icon x-small dark @click="dialogDatosFiscales = true">
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                </v-card-title>
                <v-card-text>
                  <v-row>
                    <v-col cols="12">
                      <!-- Se visualizará el chip correspondiente dependiendo de la información de los datos fIscales del usuario-->
                      <v-chip v-if="this.datosFiscales.datosDeFacturacion.tipoPersona == 1" class="mr-2 font-weight-bold">
                        <v-icon left>
                          mdi-account
                        </v-icon>
                        Persona física
                      </v-chip>
                      <v-chip v-else class="mr-2 font-weight-bold">
                        <v-icon left>
                          mdi-domain
                        </v-icon>
                        Persona moral
                      </v-chip>
                    </v-col>
                    <v-col cols="6" md="6">
                      Régimen:
                    </v-col>
                    <v-col cols="6" md="6">
                      {{ this.nombreRegimen }}
                    </v-col>
                    <v-col cols="6" md="6">
                      RFC:
                    </v-col>
                    <v-col cols="6" md="6">
                      {{ this.datosFiscales.datosDeFacturacion.rfc }}
                    </v-col>


                      <v-col cols="6" md="6" v-if="this.datosFiscales.datosDeFacturacion.tipoPersona == 1">
                        Nombre:
                      </v-col>
                      <v-col cols="6" md="6" v-else>
                        Razón Social:
                      </v-col>

                      <v-col cols="6" md="6" v-if="this.datosFiscales.datosDeFacturacion.tipoPersona == 1">
                        {{ this.datosFiscales.datosDeFacturacion.nombre + " " + this.datosFiscales.datosDeFacturacion.apellidoPaterno + " " +  this.datosFiscales.datosDeFacturacion.apellidoMaterno  }}
                      </v-col>
                      <v-col cols="6" md="6" v-else>
                        {{ this.datosFiscales.datosDeFacturacion.razonSocial }}
                      </v-col>

                    <v-col cols="6" md="6">
                      CP:
                    </v-col>
                    <v-col cols="6" md="6">
                      {{ this.datosFiscales.datosDeFacturacion.codigoPostal }}
                    </v-col>
                    <v-col cols="6" md="6">
                      Uso de CFDI:
                    </v-col>
                    <v-col cols="6" md="6">
                      {{ this.nombreUsoCfdi}}
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>

              <v-row v-else>
                <v-col cols="12">
                  <img src="../../assets/boletin_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
                </v-col>
                <v-col cols="12">
                  <h2 style="font-family: Nunito;" class="text--disabled">No tiene datos fiscales registrados</h2>
                </v-col>
                <v-col cols="12">
                  <v-btn rounded @click="onClickAdd"><span style="font-family: Nunito;" class="primary--text">Agregar</span></v-btn>
                </v-col>
              </v-row>

            </v-tab-item>

            </v-tabs>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>

    <dialogo v-model="dialogEditarUsuario" title="Editar usuario">
      <v-row>
        <v-col cols="12" md="6">
          <v-text-field v-model="usuarioParaEditar.nombre" label="Nombre" autofocus/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field v-model="usuarioParaEditar.apellidoPaterno" label="Apellido Paterno"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field v-model="usuarioParaEditar.apellidoMaterno" label="Apellido Materno"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field :value="usuarioParaEditar.email" label="Correo" disabled/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field v-model="usuarioParaEditar.telefono" label="Teléfono"/>
        </v-col>
      </v-row>

      <template slot="actions">
        <v-btn text color="primary" plain @click="actualizarUsuario">Guardar</v-btn>
      </template>
    </dialogo>

    <dialogo v-model="dialogDatosFiscales" title="Datos fiscales">
      <v-form v-model="formDatosFiscales" lazy-validation ref="formDatosFiscales">

      <v-row>
        <v-col cols="12" class="text--disabled font-weight-bold caption">
          Favor de capturar razón social con el formato que aparece en su constancia de situación fiscal.
          <br>
          Si cuenta con más de 1 régimen fiscal favor de confirmar al que desea que se emita su factura.
        </v-col>

        <v-flex  cols="12" style="text-align: center">
          <div>Capture datos o adjunte constancia de situación fiscal</div>
          <div>
            <v-btn  color="primary" class="text-none" rounded depressed :loading="isSelecting" @click="onButtonClick">
              <img src="../../assets/cloud-upload.svg"> Subir
            </v-btn>
            <input ref="uploader" class="d-none" type="file"  accept="application/pdf"  @change="onFileChanged">
          </div>

        </v-flex>

        <v-col cols="12">
          <v-radio-group v-model="tipoPersona" row>
            <v-radio value="moral" label="Persona Moral"/>
            <v-radio value="fisica" label="Persona física"/>
          </v-radio-group>
        </v-col>
        <v-col cols="12" md="6">
          <v-select label="Régimen fiscal" :rules="nameRules" :items="regFiscal" item-text="regimen" item-value="idRegimen"
                     :value="taxDataEntity.regime" @change="changeUsoCfdi($event)" />
        </v-col>
        <v-col cols="12" md="6" v-if="typePerson == 2">
          <v-text-field label="Razón social" :rules="nameRules" :value="taxDataEntity.socialReason"
                        @keyup="refreshInputData({socialReason: $event.target.value.toUpperCase()})"/>
        </v-col>
        <v-col cols="12" md="6" v-if="typePerson == 1">
          <v-text-field label="Nombre" :rules="nameRules"  :value="taxDataEntity.names"
                        @keyup="refreshInputData({names: $event.target.value.toUpperCase()})"/>
        </v-col>
        <v-col cols="12" md="6" v-if="typePerson == 1">
          <v-text-field label="Apellido Paterno" :rules="nameRules" :value="taxDataEntity.lastName"
                        @keyup="refreshInputData({lastName: $event.target.value.toUpperCase()})"/>
        </v-col>
        <v-col cols="12" md="6" v-if="typePerson == 1">
          <v-text-field label="Apellido Materno" :rules="nameRules" :value="taxDataEntity.secondLastName"
                        @keyup="refreshInputData({secondLastName: $event.target.value.toUpperCase()})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="RFC" :rules="rfcRules" :value="taxDataEntity.rfc"
                        @keyup="refreshInputData({rfc: $event.target.value.toUpperCase()})"/>
        </v-col>

        <v-col cols="12" md="6">

            <v-text-field label="Código postal"  :value="taxDataEntity.cp" ref="codigoPostal" type="number" :rules="nameRules"
                          @keyup.exact="refreshInputData({cp: $event.target.value})"
                          @keyup="validateCp"/>

            <v-btn color="primary" :disabled="!taxDataEntity.cp || taxDataEntity.cp.length !== 5" @click="getCP()">
              {{"buscar"}}
            </v-btn>

        </v-col>

        <v-col cols="12" md="6">
          <v-text-field label="Estado" :rules="nameRules" :value="taxDataEntity.state"
                        @keyup="refreshInputData({state: $event.target.value})" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="Municipio" :rules="nameRules" :value="taxDataEntity.town"
                        @keyup="refreshInputData({town: $event.target.value})" />
        </v-col>

        <v-col cols="12" md="6">
          <v-text-field v-if="manualColony" :value="taxDataEntity.colony" ref="colonia"  label="Colonia"
                        @keyup="refreshInputData({colony: $event.target.value})" />

          <v-select v-else :value="taxDataEntity.colony"  :items="cpData.colonies" ref="colonia" label="Colonia"
                    @change="refreshInputData({colony: $event})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="Tipo de vialidad" :rules="nameRules" :value="taxDataEntity.highwayType"
                        @keyup="refreshInputData({highwayType: $event.target.value})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="Nombre de vialidad" :rules="nameRules" :value="taxDataEntity.highwayName"
                        @keyup="refreshInputData({highwayName: $event.target.value})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="Ext." :rules="nameRules" :value="taxDataEntity.ext"
                        @keyup="refreshInputData({ext: $event.target.value})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field  label="Int." :value="taxDataEntity.numInt"
                        @keyup="refreshInputData({numInt: $event.target.value})"/>
        </v-col>
        <v-col cols="12" md="6">
          <v-select label="Uso de CFDI" :rules="nameRules" :items="usoCFDI" :value="taxDataEntity.cfdi"
                    @change="refreshInputData({cfdi: $event})"/>
        </v-col>
      </v-row>

      </v-form>
      <template slot="actions" v-if="this.editFlag">
        <v-btn text color="primary" plain @click="actualizarDatosFiscales">Actualizar</v-btn>
      </template>
      <template slot="actions" v-else>
        <v-btn text color="primary" plain @click="guardarDatosFiscales">Guardar</v-btn>
      </template>
    </dialogo>
  </wrapper>
</template>

<script>
  import ClientesInternetService from "../../services/ClientesInternetService";
  import DatosFiscalesService from "../../services/DatosFiscalesService";
  import CatalogosService from "../../services/CatalogosService";
  import AuthConst from "../../config/AuthConst";
  import axios from "axios";

  import MenusService from "@/services/MenusService";

  const reRFC = /^([A-ZÑ&]{3,4}) ?(\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])) ?([A-Z\d]{2})([A\d])$/

  export default {
    name: "InformacionPersonal",
    data() {
      return {
        dialogEditarUsuario: false,
        typePerson: 0,
        usuarioParaEditar: {},
        dialogDatosFiscales: false,
        editFlag: false,
        formDatosFiscales: false,
        nameRules: [v => !!v || 'Este campo es requerido'],
        rfcRules: [v => !!v || 'Este campo es requerido', v => (v && (v.length == 12 || v.length == 13) && reRFC.test(v)) || 'Ingrese un RFC válido'],
        regFiscal: [],
        //Tipos de regimen de ejemplo
        personaMoral: [
          'Actividades Agrícolas, Ganaderas, Silvícolas y Pesqueras',
          'Coordinados',
          'General de Ley Personas Morales',
          'Opcional para Grupos de Sociedades',
          'Personas Morales con Fines no Lucrativos',
          'Régimen Simplificado de Confianza',
          'Residentes en el Extranjero sin Establecimiento Permanente en México',
          'Sociedades Cooperativas de Producción que optan por diferir sus ingresos'
        ],
        personaFisica: [
          'Arrendamiento',
          'Demás ingresos',
          'Incorporación Fiscal',
          'Ingresos por Dividendos (socios y accionistas)',
          'Ingresos por intereses',
          'Personas Físicas con Actividades Empresariales y Profesionales',
          'Régimen de Enajenación o Adquisición de Bienes',
          'Régimen de las Actividades Empresariales con ingresos a través de Plataformas Tecnológicas',
          'Régimen de los ingresos por obtención de premios',
          'Régimen Simplificado de Confianza',
          'Residentes en el Extranjero sin Establecimiento Permanente en México',
          'Sin obligaciones fiscales',
          'Sueldos y Salarios e Ingresos Asimilados a Salarios'
        ],
        tipoPersona: null,
        usoCFDI: [
          {value: 'G01', text: 'Adquisición de mercancías'},
          {value: 'G03', text: 'Gastos en general'}
        ],
        taxDataEntity: {},
        cpData: {
          colonies: [],
        },
        manualColony: true,
        regSinEfectosFiscales: [605, 607, 608, 610, 611, 614, 615, 616],
        datosFiscales: [],
        isSelecting: false,
        nombreRegimen:"",
        nombreUsoCfdi:""
      }
    },
    async created() {
      let vm = this;
      this.loadDataEdit(vm);
    },
    watch: {
      tipoPersona(val) {
        console.log("tipoPersona --->", val);
        if (val) {
          this.getRegimenFiscal()
        } else {
          this.regFiscal = []
        }
      },
      dialogDatosFiscales(val) {
        if (val) {

          if(this.editFlag){
            this.abriEditarDatosFiscales()
          }else{
            const vm = this;
            this.$nextTick(() => (vm.$refs.formDatosFiscales.reset()));
          }

        }
      }
    },
    methods: {
      async loadDataEdit(vm) {
        this.$loader = true
        try {
            vm.datosFiscales = await ClientesInternetService.getDataDatosFiscales(vm.$usuario.id);
            //vm.datosFiscales = await ClientesInternetService.getDataDatosFiscales(1)
            vm.regFiscal = await CatalogosService.getCatRegimen(vm.datosFiscales.datosDeFacturacion.tipoPersona);
            if (this.datosFiscales.datosDeFacturacion != null) {
              vm.regFiscal.forEach(regimenData => {
                if (regimenData.idRegimen == vm.datosFiscales.datosDeFacturacion.idRegimenFiscal) {
                  vm.nombreRegimen = regimenData.regimen;
                }
              });

              vm.usoCFDI = [
                {value: 'S01', text: 'Sin efectos fiscales'},
                {value: 'G01', text: 'Adquisición de mercancías'},
                {value: 'G03', text: 'Gastos en general'}
              ];

              vm.usoCFDI.forEach(uso => {
                if (uso.value == vm.datosFiscales.datosDeFacturacion.usoCfdi) {
                  vm.nombreUsoCfdi = uso.text;
                }
              });

              this.editFlag = true;
            } else {
              this.editFlag = false;
            }
        } catch (e) {
          console.log('error : ',e);
        }
        this.$loader = false
      },
      abrirDialogEditar() {
        this.usuarioParaEditar = JSON.parse(JSON.stringify(this.$usuario))
        this.dialogEditarUsuario = true
      },
      abriEditarDatosFiscales(){

        this.taxDataEntity = JSON.parse(JSON.stringify(this.taxDataEntity))
        this.$set(this.taxDataEntity, 'rfc', this.datosFiscales.datosDeFacturacion.rfc);
        this.$set(this.taxDataEntity, 'cp', this.datosFiscales.datosDeFacturacion.codigoPostal);
        this.$set(this.taxDataEntity, 'highwayName', this.datosFiscales.datosDeFacturacion.direccion);
        this.$set(this.taxDataEntity, 'colony', this.datosFiscales.datosDeFacturacion.colonia);
        this.$set(this.taxDataEntity, 'highwayType', this.datosFiscales.datosDeFacturacion.tipoDeVialidad);
        this.$set(this.taxDataEntity, 'town', this.datosFiscales.datosDeFacturacion.municipio);
        this.$set(this.taxDataEntity, 'ext', this.datosFiscales.datosDeFacturacion.numExt);
        this.$set(this.taxDataEntity, 'numInt', this.datosFiscales.datosDeFacturacion.numInt);
        this.$set(this.taxDataEntity, 'state', this.datosFiscales.datosDeFacturacion.estado);
        this.$set(this.taxDataEntity, 'socialReason', this.datosFiscales.datosDeFacturacion.razonSocial);
        this.$set(this.taxDataEntity, 'names', this.datosFiscales.datosDeFacturacion.nombre);
        this.$set(this.taxDataEntity, 'lastName', this.datosFiscales.datosDeFacturacion.apellidoPaterno);
        this.$set(this.taxDataEntity, 'secondLastName', this.datosFiscales.datosDeFacturacion.apellidoMaterno);
        this.$set(this.taxDataEntity, 'regime', this.datosFiscales.datosDeFacturacion.idRegimenFiscal);
        this.$set(this.taxDataEntity, 'cfdi', this.datosFiscales.datosDeFacturacion.usoCfdi);
        this.$set(this.taxDataEntity, 'idDatosDeFacturacion', this.datosFiscales.datosDeFacturacion.idDatosDeFacturacion);
        this.$set(this.taxDataEntity, 'tipoPersona', this.datosFiscales.datosDeFacturacion.tipoPersona);

        let clave;

        this.regFiscal.forEach(regimenData => {
          if (regimenData.idRegimen == this.datosFiscales.datosDeFacturacion.idRegimenFiscal) {
            clave = regimenData.clave;
          }
        });
        console.log("event Uso de clave", clave);
        if (this.regSinEfectosFiscales.includes(clave)) {

          this.usoCFDI = [
            {value: 'S01', text: 'Sin efectos fiscales'},
          ],
            this.taxDataEntity.cfdi = 'S01';

        } else {
          this.usoCFDI = [
            {value: 'G01', text: 'Adquisición de mercancías'},
            {value: 'G03', text: 'Gastos en general'}
          ];
        }

        console.log("this.taxDataEntity ",this.taxDataEntity);
        this.$set(this, 'tipoPersona', this.datosFiscales.datosDeFacturacion.tipoPersona == 2 ? 'moral' : 'fisica');
        //setTaxDataFromPdf(this, response);

      },
      actualizarUsuario() {
        const vm = this
        vm.$loader = true
        ClientesInternetService.updateClienteInternet(vm.usuarioParaEditar)
          .then(clienteInternetActualizado => {
            vm.$usuario = {...vm.$usuario, ...clienteInternetActualizado}
            localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(vm.$usuario))
            vm.dialogEditarUsuario = false
            vm.$showAlerta('600', 'success', 'mdi-check-circle', 'Información actualizada.', true)
          })
          .catch(() => {
            vm.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la información, inténte más tarde.', true)
          })
          .finally(() => (vm.$loader = false))
      },
      async getRegimenFiscal() {
        let typePersonSelect;
        this.refreshInputData({personType: this.tipoPersona})
        if (this.tipoPersona === 'moral') {
          typePersonSelect = 2;
          this.typePerson = 2;
        } else {
          typePersonSelect = 1;
          this.typePerson = 1;
        }

        if(!this.editFlag){
          this.taxDataEntity.cfdi = '';
        }
        this.$loader = true;
        this.regFiscal = await CatalogosService.getCatRegimen(typePersonSelect);
        this.$loader = false;
        validatePdfRegime(this);
      },
      guardarDatosFiscales() {
        const vm = this;
        this.$loader = true;

        if (this.$refs.formDatosFiscales.validate()) {
          vm.$set(vm.taxDataEntity, 'userId',  this.$usuario.id);
          ClientesInternetService.saveTaxData(vm.taxDataEntity).then(response =>{
            validateSaveTaxResponse(vm,response);
          }).catch(() => {
              vm.showAlertMessage('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la información, inténte más tarde.', true)
            }).finally(()=>{
            this.$loader = false;
          });
        }
      },
      actualizarDatosFiscales(){

        const vm = this;
        if (vm.$refs.formDatosFiscales.validate()) {

          const datosFiaclaesEntity = new Object();
          datosFiaclaesEntity.idDatosDeFacturacion = vm.taxDataEntity.idDatosDeFacturacion;
          datosFiaclaesEntity.usoCfdi = vm.taxDataEntity.cfdi;
          datosFiaclaesEntity.codigoPostal = vm.taxDataEntity.cp;
          datosFiaclaesEntity.colonia = vm.taxDataEntity.colony;
          datosFiaclaesEntity.nombre =  vm.typePerson == 2 ? "" : vm.taxDataEntity.names;
          datosFiaclaesEntity.apellidoPaterno = vm.typePerson == 2 ? "" : vm.taxDataEntity.lastName;
          datosFiaclaesEntity.apellidoMaterno = vm.typePerson == 2 ? "" : vm.taxDataEntity.secondLastName;
          datosFiaclaesEntity.estado = vm.taxDataEntity.state;
          datosFiaclaesEntity.razonSocial = vm.typePerson == 2 ?vm.taxDataEntity.socialReason : "";
          datosFiaclaesEntity.numExt = vm.taxDataEntity.ext;
          datosFiaclaesEntity.numInt = vm.taxDataEntity.numInt == null ? "" : vm.taxDataEntity.numInt;
          datosFiaclaesEntity.rfc = vm.taxDataEntity.rfc;
          datosFiaclaesEntity.tipoDeVialidad = vm.taxDataEntity.highwayType;
          datosFiaclaesEntity.direccion = vm.taxDataEntity.highwayName;
          datosFiaclaesEntity.tipoPersona = vm.typePerson;
          datosFiaclaesEntity.idRegimenFiscal = vm.taxDataEntity.regime;
          datosFiaclaesEntity.municipio = vm.taxDataEntity.town;

          console.log("datosFiaclaesEntity ", datosFiaclaesEntity)
          DatosFiscalesService.updateDatosFiscales(datosFiaclaesEntity).then(response =>{
            console.log('UPDATE response: ',response);
            validateUpdateResponse(vm,response);
            this.loadDataEdit(vm);
          }).catch(() => {
              vm.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la información, inténte más tarde.', true)
            });

          //console.log("DatosFiscalesActualizados ", DatosFiscalesActualizados)
        }
      },
      getCP() {
        let vm = this;
        if (vm.taxDataEntity.cp !== undefined &&
          vm.taxDataEntity.cp && vm.taxDataEntity.cp.length === 5) {
          vm.$loader = true;
          vm.manualColony = false;
          let cp = vm.taxDataEntity.cp;

          axios.get('https://cp.vjp.io/cp/' + cp)
            .then(response => {
              if (response.data.length > 0) {
                vm.cpData.colonies = response.data.map(colony => colony.asentamiento);
                vm.refreshInputData({state: response.data[0].estado, town: response.data[0].municipio})
              }
            }).catch(function (error) {
            console.log('error : ', error);

          }).then(() => {
            vm.$loader = false;
          });
        }
      },
      changeUsoCfdi(event) {
        let vm = this;
        let clave;
        console.log("event Uso de CFDI", event);
        console.log("event Uso de regFiscal", vm.regFiscal);

        vm.regFiscal.forEach(regimenData => {
          if (regimenData.idRegimen == event) {
            clave = regimenData.clave;
          }
        });
        console.log("event Uso de clave", clave);
        if (vm.regSinEfectosFiscales.includes(clave)) {

          vm.usoCFDI = [
            {value: 'S01', text: 'Sin efectos fiscales'},
          ],
            vm.taxDataEntity.cfdi = 'S01';

        } else {
          vm.taxDataEntity.cfdi = '';
          vm.usoCFDI = [
            {value: 'G01', text: 'Adquisición de mercancías'},
            {value: 'G03', text: 'Gastos en general'}
          ];
        }
        vm.refreshInputData({regime: event})
      },
      refreshInputData(values) {

        this.taxDataEntity = {...this.taxDataEntity, ...values}
        this.$emit("input", this.taxDataEntity);

      },
      validateCp(event) {
        let cp = event.target.value;
        if (cp && cp.length !== 5) {
          this.manualColony = true;
        }
      },
      onButtonClick() {
        this.isSelecting = true
        window.addEventListener('focus', () => {
          this.isSelecting = false
        }, {once: true})

        this.$refs.uploader.click()
      },
      onFileChanged(e) {
        let vm = this;
        if (e.target.files && e.target.files.length > 0) {
          this.$loader = true;
          this.selectedFile = e.target.files[0];
          const formData = new FormData();
          formData.append("pdfFile", e.target.files[0]);
          ClientesInternetService.getPdfData(formData).then(response => {
            console.log('response pdf :  ', response);
            validatePdfResponse(vm,response);
            validatePdfRegime(vm);
          })
            .catch((err) => {
              console.log('catch pdf :  ',err);
              cleanPdfData(vm);
              vm.$showAlertMessage('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la información, inténte más tarde.', true)
            }).finally(()=>{
            this.$loader = false;
          });

        }else{
          this.$loader = false;
          cleanPdfData(vm);
          vm.$showAlertMessage('600', 'error', 'mdi-alert-circle', 'Por favor seleccione un Pdf', true)
        }

      },

      onClickAdd(){
        this.dialogDatosFiscales = true;
      }
    }
  }

  const validatePdfRegime=( vm ) => {
    if(vm.regFiscal.length> 0 && vm.taxDataEntity.pdfRegime !== undefined
      &&  vm.taxDataEntity.pdfRegime.length > 0){
      let catRegime;
      let pdfRegime = vm.taxDataEntity.pdfRegime.replace(/\s/g, "").toUpperCase();

      vm.regFiscal.forEach((element)=>{
        catRegime = element.regimen.replace(/\s/g, "").toUpperCase();
        if(pdfRegime.includes(catRegime)){
          vm.$set(vm.taxDataEntity, 'regime', element.idRegimen);
        }
      });
    }else if(vm.regFiscal.length === 0){
      vm.getRegimenFiscal();
    }
  }

  const validatePdfResponse = (vm, response)=>{
    if(response != null && response.status != null ){
      switch (response.status.code){
        case '01':
          setTaxDataFromPdf(vm, response);
          break;
        case '02':
          console.log('PDF Code 02')
          cleanPdfData(vm);
          vm.$showAlertMessage('600', 'error', 'mdi-alert-circle', 'Por favor eliga una constancia de situación fiscal válida', true)
          break;
      }
    }
  }

  const cleanPdfData= (vm)=>{
    vm.$refs.uploader.value=null;
    vm.$set(vm , 'taxDataEntity', {});
  }

  const validateUpdateResponse = (vm, response)=>{
    if(response != null){
      vm.$set(vm, 'dialogDatosFiscales', false);
      vm.$showAlertMessage('600', 'success', 'mdi-alert-circle', 'Datos fiscales editados correctamente', true)

    }
  }

  const validateSaveTaxResponse = (vm, response)=>{
    if(response != null && response.status != null ){
      switch (response.status.code){
        case '01':
          vm.loadDataEdit(vm);
          vm.$set(vm, 'dialogDatosFiscales', false);
          vm.$showAlertMessage('600', 'success', 'mdi-alert-circle', 'Datos fiscales guardados correctamente', true)
          break;
        case '02':
          vm.$showAlertMessage('600', 'error', 'mdi-alert-circle', 'Los datos de facturación ya existen, no se pueden agregar', true)
          break;
        default:
          vm.$showAlertMessage('600', 'error', 'mdi-alert-circle', 'Por el momento no se pudo guardar la información, intente más tarde', true)
          break;
      }
    }
  }


  const setTaxDataFromPdf = (vm,response)=> {
    if (response.data != null) {
      vm.$set(vm.taxDataEntity, 'rfc', response.data.rfc);
      vm.$set(vm.taxDataEntity, 'cp', response.data.cp);
      vm.$set(vm.taxDataEntity, 'highwayName', response.data.highwayName);
      vm.$set(vm.taxDataEntity, 'colony', response.data.colony);
      vm.$set(vm.taxDataEntity, 'highwayType', response.data.highwayType);
      vm.$set(vm.taxDataEntity, 'town', response.data.town);
      vm.$set(vm.taxDataEntity, 'ext', response.data.ext);
      vm.$set(vm.taxDataEntity, 'numInt', response.data.numInt);
      vm.$set(vm.taxDataEntity, 'state', response.data.state);
      vm.$set(vm.taxDataEntity, 'socialReason', response.data.socialReason);
      vm.$set(vm.taxDataEntity, 'names', response.data.names);
      vm.$set(vm.taxDataEntity, 'lastName', response.data.lastName);
      vm.$set(vm.taxDataEntity, 'secondLastName', response.data.secondLastName);
      vm.$set(vm.taxDataEntity, 'pdfRegime', response.data.regime);
      vm.$set(vm, 'tipoPersona', response.data.socialReason.length > 0 ? 'moral' : 'fisica');

    }
  }


</script>

