import AuthConst from '../config/AuthConst';
import http from "./http";


/**
 * Catalog that obtains the type of person regime
 * **/
const getCatRegimen = (typePerson) => {
  let url = AuthConst.BASE_URL + '/datos-fiscales/regimen/' + typePerson;
  return http.get(url)
};


export default { getCatRegimen }
